<template>
    <el-main>
      <el-row>
        <el-col :span="24">
           <el-button style="float:left" type="primary" icon="el-icon-back" @click="goback()"></el-button>
          <el-button style="float:left"  type="primary" icon="el-icon-refresh" @click="init()"></el-button>
          <div class="demonstration h_title">{{querymap.tablename}}<font> </font></div>
          <el-button  type="primary" icon="el-icon-plus" @click="handleData()"></el-button>
        </el-col>
      </el-row>

      <el-table :data="tableData"  height="500"   :row-style="{height:'40px'}"  
    :cell-style="{padding:'0'}"
    style="font-size: 15px">
       
        <!--el-table-column :label="getlan('column_name')" >
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.column_name }}</span>
          </template>
        </el-table-column>
        <el-table-column :label="getlan('data_type')" >
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.data_type }}</span>
          </template>
        </el-table-column-->
    <el-table-column 
    v-for="(item,key) in tableData[0]"  :key="key"
    :prop="key" :label="key" >
  </el-table-column>

        <el-table-column :label="getlan('operate')" width="260">
          <template slot-scope="scope">
              <el-button size="small"   icon="el-icon-plus" @click="handleAdd(scope.$index, scope.row)"></el-button>
             <el-button size="small"   icon="el-icon-edit" @click="handleData(scope.$index, scope.row)"></el-button>
             <el-button size="small" icon="el-icon-delete"  @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

<div class="block">
            <span class="demonstration"></span>
           <el-pagination 
                layout="prev, next" 
                @size-change="handleSizeChange"
                @current-change="current_change"
                :page-sizes="[50,200, 300]"
                :page-size="this.pagesize"
                :total="this.total">
            </el-pagination>
        </div>

     <el-dialog :title="getlan('RunSQL')" :visible.sync="dialogServer"  :width="this.getDialogWidth()"   :show-close="true" :close-on-click-modal="false" :close-on-press-escape="false">
        <el-form :model="form" :rules="rules" ref="form" class="el-form--label-top">
          <el-form-item :label="getlan('RunSQL')" prop="sqlString">
            	<el-input type="textarea" :autosize="{ minRows: 6}" placeholder="sqlString" v-model="sqlString"></el-input>
          </el-form-item>
        
        </el-form>
        <span slot="footer" class="dialog-footer">
                <el-button @click="handleClose('form')">{{getlan('cancel')}}</el-button>
                <el-button type="primary" @click="comfirmRunSql()">{{getlan('Execute')}}</el-button>
            </span>
      </el-dialog>
    </el-main>
</template>

<script>
import Qs from 'qs'
    import{Type,AddData,DeleteData,GetList} from "@/api/index"
     import{getDay,getDateTime,getReqUrl,getLang,getValue,setValue} from "@/utils/pub"
    export default {
        name: 'App',
        data() {
        
            return {
              pageid: 1,
              total:0,
            pagesize: 50,
              loadFinish:0,
               tableData:[],
              CategoryName:'',
              sqlString:'',
             dialogServer:false,
              belong:this.$route.params.belong,
              form:{
                id:0,
                warehousename:'',
                 remark:'',
                table:'column',
                tableData:[],
                mode:'',
                userid:getValue("userid"),
                 belong:this.$route.params.belong,
              },
               querymap:{ 
               pageid:this.pageid,
                pagesize:this.pagesize,
                table:'column',
               tablename:this.$route.params.tablename,
              },
              rules:{
               warehousename:[
                  { required: true, message: 'Please input name', trigger: 'change' },
                  { min: 1, max: 30, message: 'length 1 - 30', trigger: 'change' }
                ],
              }
            }
        },
        created(){
          this.belong=this.$route.params.belong;
          this.init();
        },
         methods: {
          getlan(name){ var str=this.$t('lang.'+name+''); if(str.indexOf("lang.")>=0){ return name; }return str },
          init(){
            //this.total=this.tableData.length;
          if(this.$route.params.dbname!=null){
             this.querymap.dbname=this.$route.params.dbname;
              setValue("tablename",this.$route.params.dbname)
          }else{
           this.querymap.dbname=getValue("tablename");
          }

            GetList(Qs.stringify(this.querymap)).then(res=>{
           
              this.tableData = res.data.data;
               this.total=res.data.total;
               this.pagesize=res.data.pagesize;
            })

          },
           goback(){
	           this.$router.push({name:'tables'});
          },
            handleAdd(index, row){
              var columnname=row["column_name"];
              var datatype=row["data_type"];

            this.errorMessage="";
            this.sqlString="alter table "+this.querymap.tablename+" add  column xxx varchar(10); "
             this.dialogServer = true;
            
          },

          handleData(index, row){

var columnname=row["column_name"];
var datatype=row["data_type"];
              this.sqlString="ALTER TABLE   "+this.querymap.tablename+" change  column "+columnname+" new"+columnname+" "+datatype;
             // this.sqlString+="--Alter table  "+this.querymap.tablename+"  modify "+columnname+" char(10) "
              this.dialogServer = true;
            
          },
           handleDelete(index, row) {
             var columnname=row["column_name"];
            var datatype=row["data_type"];
             this.sqlString="ALTER TABLE "+this.querymap.tablename+" DROP COLUMN "+columnname
              this.dialogServer = true;
          },
          handleClose(formName){
            this.dialogServer = false;
            this.form = {
              id:0,
                warehousename:'',

            }
            this.$refs['form'].resetFields();
          },//分页
        current_change:function(pageid){
            this.pageid = pageid;
  GetList(Qs.stringify(this.querymap)).then(res=>{
            
              this.tableData = res.data.data;
               this.total=res.data.total;
               this.pagesize=res.data.pagesize;
            })

        },
        handleSizeChange(val) {
            this.pagesize = val;
            
        },
         
         
          create(form){
           
            if(form.warehousename==''){
             this.alert_ok('warehousename is null')
             return
            }
             
                this.form.table='warehouse',
                 this.form.userid=getValue("userid"),
                  this.form.belong=this.$route.params.belong,
          form.tableData="{\"id\":\""+this.form.id+"\",\"warehousename\":\"" +form.warehousename+ "\",\"remark\":\"" +form.remark+ "\"}|";

            AddData(Qs.stringify(this.form)).then(res=>{
             
              if(res.data.code==1){
                
                this.dialogServer = false;
                  this.form.warehousename=""
              this.form.remark=""
                this.init();
                this.$message({message: 'success',type: 'success'});
              }else{
                if(res.data.message=="exists"){
                    this.$message.error(this.getlan('Exists'));
                }else{
                   this.$message.error(res.data.message);
                }
              }
            })
          },
           comfirmRunSql(){
         
    this.$confirm( this.sqlString,  this.getlan('Execute'), {
              confirmButtonText:  this.getlan('Execute'),
              confirmButtonClass: 'el-button--danger',
              cancelButtonText:  this.getlan('cancel'),
              type: 'warning'
            }).then(() => {
                this.runsql()
            }).catch(() => {});
          },

       runsql() {
          
          if(this.sqlString==''){
             this.alert_ok('sql is null')
             return
            }
             
          var  params={ 
              sql:this.sqlString,
              table:'runsql'
              }
             // console.log("this.sql="+this.sqlString)
                this.errorMessage="";
            AddData(Qs.stringify(params)).then(res=>{
             
              if(res.data.code==1){
                this.dialogServer = false;
                this.tableData=res.data.data;
                this.$message({message:res.data.message,type: 'success'});
              }else{
                   if(res.data.message!=null){
                     this.errorMessage=res.data.message
                     this.alert_ok(res.data.message)
                   }
              }
            })

          }
        }
    }
</script>

<style lang="scss" scoped>
  .el-main {
    margin: 0px 0 0 160px;
    padding: 10px;
    .el-col {
      text-align: right;
    }
    .el-form--label-top .el-form-item__label {
      padding: 0;
    }
    .el-button.add {
      background-color: #4caf50;
      border: 0;
      font-size: 12px;
    }
     .h_title{
      margin-left:20px;
      float: left;
      line-height: 40px;
      font{
        color: #4caf50;
      }
    }
  }
</style>



